<template>
    <div>
        <h4 class="display-6">用户添加及搜索</h4>
    <form action="http://chhtcht.m.8i7.cn/writeUser.php" method="post">
      名字：
      <input type="text" name="name">
      密码：
      <input type="text" name="pass">
      邮箱：
      <input type="email" name="email">
      <input type="submit" value="添加">
    </form>
    <label for>搜索名称关键字</label>
    <input type="text" v-model="keywords">

    <div class="tab">
      <table class="table">
        <thead>
          <tr>
            <th>id</th>
            <th>name</th>
            <th>pass</th>
            <th>email</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in search(keywords)" :key="item.id">
            <td>{{item.id}}</td>
            <td>{{item.name}}</td>
            <td>{{item.pass}}</td>
            <td>{{item.email}}</td>
          </tr>
        </tbody>
      </table>
    </div>
    </div>
</template>

<script>
export default {
  data() {
    return {
      id: "",
      name: "",
      pass: "",
      email: "",
      keywords: "",
      userInfo: []
    };
  },
  created() {
    this.getUserInfo();
  },
  methods: {
    search(keywords) {
      var newList = [];
      return (newList = this.userInfo.filter(item => {
        if (item.name.includes(keywords)) {
          return item;
        }
      }));
    },
    getUserInfo() {
      this.axios.post("http://chhtcht.m.8i7.cn/readuser.php").then(res => {
        this.userInfo = res.data;
      });
    }
  }
};
</script>