<template>
    <div>
          <div class="modal fade" id="myModal">
        <div class="modal-dialog">
          <div class="modal-content">
            <!-- 模态框头部 -->
            <div class="modal-header">
              <h4 class="modal-title">登录</h4>
              <button type="button" class="close" data-dismiss="modal">&times;</button>
            </div>

            <!-- 模态框主体 -->
            <div class="modal-body">
              <form action method="post">
                <div class="input-group mb-3">
                  <div class="input-group-prepend">
                    <span class="input-group-text">&nbsp;用户名</span>
                  </div>
                  <input type="text" class="form-control" placeholder="请输入用户名" id="usr" name="user" v-model="user">
                </div>

                <div class="input-group mb-3">
                  <div class="input-group-prepend">
                    <span class="input-group-text">&nbsp;&nbsp;密码&nbsp;&nbsp;</span>
                  </div>
                  <input
                    type="password"
                    class="form-control"
                    placeholder="*****"
                    id="usr"
                    name="pass" v-model="pass"
                  >
                </div>
                <button type="submit" class="btn btn-primary btn-block" @click.prevent="login" data-dismiss="modal">登录</button>
              </form>
            </div>
            <!-- 模态框底部 -->
            <div class="modal-footer">
              <button class="btn btn-primary" @click.prevent="open">立即注册</button>
              <button type="button" class="btn btn-secondary" data-dismiss="modal">关闭</button>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>


<script>
export default {
  data(){
    return{
      user:'',
      pass:'',
      loginShow:flase,
    }
  },
  methods:{
  login(){
    if(this.user=='ben'&&this.pass=="ben"){
      this.$message({
          message: '恭喜，登录成功',
          type: 'success'
        });
       this.$store.commit('changeUser','ben');
       this.user='',
       this.pass=''

    }else
    {
       this.$message('用户名密码均为：  ben ');
    }
  }

}
}
</script>


