                  <a href>{{item.name}}</a>
                </span>
                <span>
                  用户id：
                  <a href>{{item.user_id}}</a>
                </span>
              </div>
              <div class="card-body ">
                <span>
                  <h6>{{item.text}}</h6>
                </span>
              </div>
              <div class="card-body">
                <!-- <img v-lazy="item.cdn_img"> -->
