                </div>
              </div>
            </div>
          </div>
          <div class="d-none d-md-block col-md-2 bg-light">侧边栏</div>
        </div>
        <div class="foot">
          <div class="col-sm-10 col-md-10 col-lg-10 col-xl-10">
            <addT></addT>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="留言版">
          <!-- 留言板显示内容 -->
            <div class="card" v-for="item in liuyan" :key="item.link">
