                <a href="" target="_blank" class="aTitle">{{item.name}}</a>
              </div>
              <div class="card-body">
                <span class="newsContent">{{item.content}}</span>
              </div>
            </div>
            <!-- 留言板 -->
        <messageBoard></messageBoard>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import messageBoard from "../subcomponents/messageBoard";
export default {
  data() {
    return {
      forumList: [],
      liuyan:[{name:'哔哩哔哩',content:'哔哩哔哩 (゜-゜)つロ 干杯'},
      {name:'acfun',content:'认真你就输了'}],
      isChoose:false,

    };
  },
  created() {
    this.getForumList();
  },
  methods: {
    getForumList() {
      this.axios
        .get("https://www.apiopen.top/satinApi?type=1&page=1")
        .then(res => {
          console.log(res);
          this.forumList = res.data.data;
        });
    },
   
  },
  components: {
    messageBoard
  }
};
</script>

<style scoped>
.card {
  margin-top: 4px;
}
.card-body img {
  width: 200px;
  height: 200px;
}
.card:hover {
  border: 1px solid darkgoldenrod;
  background-color: FloralWhite;
}
.foot {
  margin-top: 50px;
}
img{
  position: relative;
  transform: scale(1);
  transition: all ease 0.5s;
}

</style>