import Vue from 'vue'
import Router from 'vue-router'
import Home from './components/home/home'
import Forum from './components/forum/forum.vue'
import User from './components/user/user.vue'
import Video from './components/video/video.vue'
import VideoPlay from './components/video/videoPlay.vue'
import carousel from './components/subcomponents/carousel.vue'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'home',
      components:{
        default:Home,
        lunbo:carousel
      }
    },
    {
      path: '/forum',
      name: 'forum',
      component: Forum
    }, {
      path: '/User',
      name: 'user',
      components:{
        default:User,
      }
    },
   
    {
      path: '/video',
      name: 'video',
      component: Video
    },
    {
      path: '/home/video/:id',
      component: VideoPlay
    },
 
  ]
})