<template>
  <div class="container">
    <div class="card" v-for="item in newsList" :key="item.link">
      <div class="card-header">
        <a :href="item.link" target="_blank" class="aTitle">{{item.title}}</a>
      </div>
      <div class="card-body">
        <span class="newsContent">{{item.content}}</span>
      </div>
    </div>
    <div class="block hidden-sm-and-down">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage4"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="400"
      ></el-pagination>
    </div>
    <div class="hidden-sm-and-up">
       <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage4"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="100"
        layout=" prev,  next"
        :total="400"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      newsList: [],
      pageSize: 5,
      pageIndex: 0
    };
  },
  created() {
    this.getNews();
  },
  methods: {
    getImgList() {
      this.axios.get("http://chhtcht.m.8i7.cn/img.php").then(result => {
        if (result.status === 200) {
          this.imgList = result.data;
        } else {
          alert("获取图片信息失败");
        }
      });
    },
    getNews() {
      this.axios
        .post("http://api.komavideo.com/news/list/", {
          pageSize: this.pageSize,
          pageIndex: this.pageIndex
        })
        .then(res => {
          this.newsList = res.data;
        });
    },
    handleSizeChange(val) {
      // console.log(`每页 ${val} 条`);
      this.pageSize = val;
      // console.log("每页::::"+this.pageSize);
      this.getNews();
    },
    handleCurrentChange(val) {
      // console.log(`当前页: ${val}`);
      this.pageIndex = val;
      // console.log("我在"+this.pageIndex);
      this.getNews();
    }
  }
};
</script>

<style scoped>
.card-body {
  max-height: 185px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.card {
  margin: 5px 0;
}
.card-body:hover {
  background-color: #ddd;
}
</style>
