<script>
export default {
  data() {
    return {
      imgList: ""
    };
  },
  created() {
    this.getImgList();
  },
  methods: {
    getImgList() {
      this.axios.get("http://chhtcht.m.8i7.cn/img.php").then(result => {
        if (result.status === 200) {
          this.imgList = result.data;
        } else {
          alert("获取图片信息失败");
        }
      });
    }
  }
};
</script>

